@import "../../../style";

.animation_bg {
   position: fixed;
   top: 0;
   left: 0;
   z-index: 12000;

   width: 100%;
   height: 100%;

   background: var(--background);
   background-image: url('data:image/png;base64,');
   animation: transformAnim 500ms 400ms ease forwards;
}

@keyframes transformAnim {
   0% {

   }

   100% {
      transform: translateY(-100%);
   }
}
